@import "../variable";

.font-size-card-l {
    font-size: 22px;
}

.font-size-card-m {
    font-size: 20px;
}

.font-size-card-s {
    font-size: 14.5px;
}

.line-throw-text {
    text-decoration: line-through;
}

.added-product {
    //border: 1px solid #D4D4D4;
    //border-radius: 5px;
    &-img {
        height: 220px;
    }
}

.total-price {
    //border: 1px solid #ffffff;
    //border-radius: 5px;
}

.product {
    &-title {
        color: $primary-color;
        //top: 20px;
        //left: 260px;
        width: max-content;
        //left: 50%;
        //top: 50%;
        //transform: translate(-65%, -300%);
    }

    &-price {
        background-color: $primary-color;
    }
}

.quantity {
    &-district {
        max-width: 70px;
        text-align: center;
        border: 1px solid $primary-color;
        font-size: 14.5px;
    }

    &-text {

    }
}

.line-throw-text {
    color: #D4D4D4;
}

.btn {
    //&-calc {
    //border: 1px solid;
    //}

    &-plus {
        padding: 3px 14px;
        border: 1px solid;
        border-color: $logo-color;
        color: $logo-color;

        &:hover {
            border-color: $primary-color;
            color: $primary-color;
        }
    }

    &-minus {
        padding: 3px 14px;
        border: 1px solid;
        border-color: $primary-color;
        color: $primary-color;

        &:hover {
            border-color: #d58e32;
            color: #d58e32;
        }
    }
}

.sum {
    &-text {
        color: #D4D4D4;
    }

    &-district {
        color: $primary-color;
    }
}

.total {
    &-text {
        color: $primary-color;
    }

    &-district {
        color: $primary-color;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1200px) {
    .modal-dialog {
        max-width: calc(100% - 3.5rem);
    }
}

@media only screen and (min-width: 992px) {
    .added-product-container-3 {
        margin-top: -155px;
    }
}

@media only screen and (min-width: 768px) {
    .added-product-container-3 {
        margin-top: -140px;
    }
}

@media only screen and (min-width: 576px) and (max-width: 767.98px) {
    .added-product-container-3 {
        margin-top: -30%;
    }

    .font-size-card-l {
        font-size: 19px;
    }

    .font-size-card-m {
        font-size: 17px;
    }

    .font-size-card-s {
        font-size: 12.5px;
    }
}

@media only screen and (min-width: 310px) and (max-width: 575.98px) {
    .font-size-card-l {
        font-size: 20px;
    }

    .font-size-card-m {
        font-size: 18px;
    }

    .font-size-card-s {
        font-size: 12.5px !important;
    }
}

@media only screen and (min-width: 310px) and (max-width: 574.98px) {
    .product-price-container {
        max-width: 200px;
    }
}